


































	import LoadMore from '../../components/uni-load-more/uni-load-more.vue'
    import { requestip,headerList } from '@/pages/api/api.js'
	var clears;
	export default{
		components:{
			LoadMore
		},
		data(){
			return{
				data:'',
				flexblean:false,
				showloadMore:false,
				loadMoreText:'加载中...',
				browse:false,
				browseList:false,
				radioBlean:false,
				rean:false,
				crurrent:0,
				pages:1,
				addgoods:[],
				allgoods:[],
				height:0
			}
		},
		methods:{
			// 全选
			radiochech(){
				this.radioBlean=!this.radioBlean;
				this.rean=!this.rean;
				if(this.rean==true){
					for(var i=0;i<this.data.length;i++){
						this.allgoods.push(this.data[i].favorite_id);
					}
					// console.log('所有数据',this.allgoods);
				}else{
					this.allgoods=[];
					// console.log('所有数据',this.allgoods);
				}
				
			},
			// 初始化数据
			colletionInit(){
				var uid=uni.getStorageSync('userId');
				uni.request({
					url: requestip()+'/v2/duoduoke/product/getfavorite?user_id='+uid+'&page=1',
					method: 'GET',
					header:{
						"access-token":uni.getStorageSync('access-token'),
					    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
					   },
					data: {},
					success: res => {
						// console.log('收藏数据：',res.data);
						this.data=res.data.data.productList;
						// console.log('收藏',this.data)
					},
					fail: () => {},
					complete: () => {}
				});
			},
			goodCT(i){
				var a=[i];
				var that=this;
				uni.showModal({
					title:'提示',
					content:'你确定删除商品吗',
					success(res) {
						if(res.confirm){
							uni.request({
								url: 'https://appserver.dijiamai.net/v2/duoduoke/product/removefavoritenew',
								method: 'GET',
								header:{
									"access-token":uni.getStorageSync('access-token'),
								    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
								   },
								data: {
									favorite_id:a
								},
								success: res => {
									that.colletionInit();
									that.radiochech();
									that.radiochech();
								},
								fail: () => {},
								complete: () => {}
							});
						}else{
							that.rean=false;
							that.radiochech();
							that.radiochech();
						}
					}
				})
				
			},
			goodcheck(e){
				// console.log('选择',e);
				this.addgoods.push(e.detail.value[0]);
				// console.log("数据组8989",this.addgoods);
				if(e.detail.value[0]){
					var addgood=[]
					for(var i=0;i<this.addgoods.length;i++){
						if(addgood.indexOf(this.addgoods[i])==-1 && this.addgoods[i]!=undefined){
							addgood.push(this.addgoods[i]);
						}
					}
					// console.log("数据组：",addgood);
					this.addgoods=addgood;
					// console.log("数据组8888",this.addgoods);
				}else{
					
				}
				
				clears=this.addgoods;
				
			},
			removeAdd(){
				// console.log("数据组2：",this.addgoods);
				uni.request({
					url: 'https://appserver.dijiamai.net/v2/duoduoke/product/removefavoritenew',
					method: 'GET',
					header:{
						"access-token":uni.getStorageSync('access-token'),
					    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
					   },
					data: {
						favorite_id:this.allgoods
					},
					success: res => {
						this.colletionInit();
						this.addgoods=[];
						this.rean=false;
					},
					fail: () => {},
					complete: () => {}
				});
			},
			// 手机信息
			phoneInfo(){
				var that=this;
				uni.getSystemInfo({
					success(res) {
						// console.log(res);
						that.height=res.windowHeight;
					}
				})
			},
			// 删除商品
			delgood(e){
				var that=this;
				uni.showModal({
					title:'提示',
					content:'你确定删除商品吗',
				success(res) {
					if(res.confirm){
				uni.request({
					url: 'https://appserver.dijiamai.net/v2/duoduoke/product/removefavoritenew',
					method: 'GET',
					header:{
						"access-token":uni.getStorageSync('access-token'),
					    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
					   },
					data: {
						favorite_id:[e]
					},
					success: res => {
						that.colletionInit();
					},
					fail: () => {},
					complete: () => {},
					});
					}
					}
				});
			},
			mess(i){
				uni.navigateTo({
					url:'../productmess/productmess?id='+i
				})
			}
		},
		onLoad:function(){
			this.colletionInit();
			uni.hideShareMenu();
		},
		onReachBottom() {
			this.pages+=1;
			this.showloadMore=true;
			var uid=uni.getStorageSync('userId');
			uni.request({
				url: requestip()+'/v2/duoduoke/product/getfavorite?user_id='+uid+'&page='+this.pages,
				method: 'GET',
				header:{
					"access-token":uni.getStorageSync('access-token'),
				    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
				   },
				data: {},
				success: res => {
					// console.log('收藏数据：',res.data);
					// this.data=res.data.data.productList;
					for(var i=0;i<res.data.data.productList.length;i++){
						this.data.push(res.data.data.productList[i])
					}
						this.showloadMore=false;
					if(res.data.data.productList==''){
						this.loadMoreText='没有更多数据了';
						this.showloadMore=true;
					}else{
						this.loadMoreText='加载中...';
						this.showloadMore=false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onShow() {
			this.phoneInfo();
		}
	}
